import styled from 'styled-components'


export default styled.div`
  min-height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 0 2rem;
  -webkit-app-region: drag;
  
  border-bottom: 1px solid #eee;
  
`


